import React from "react";
import styled from "styled-components";

import { Button } from "../";

import sizes from "../../constants/sizes";

const Wrapper = styled.div`
  margin: 90px 0 30px;

  @media (min-width: ${sizes.mm}) {
    margin: 8% 0 3%;
  }
`;

const SocialMediaButton = styled(Button)`
  background-color: rgba(0, 0, 0, 0.5);
  display: block;
  margin: 11px 0 0 auto;
  padding: 13px;
  text-align: left;
  width: 285px;

  @media (max-width: ${sizes.m}) {
    margin-top: 14px;
    padding: 7px 15px;
    width: 160px;
  }
`;

const ButtonContent = styled.div`
  align-items: center;
  font-family: "Avenir LT Std 45 Book", sans-serif;
  display: flex;

  @media (max-width: ${sizes.m}) {
    font-size: 12px;
    letter-spacing: 0.24px;
    line-height: 14px;
  }

  & > img {
    margin-right: 16px;

    @media (max-width: ${sizes.m}) {
      margin-right: 7px;
      width: 19px;
    }
  }
`;

export default ({ socialMedias }) => (
  <Wrapper>
    {socialMedias.map((socialMedia, i) => {
      const { icon, icon2x, icon3x, link, text } = socialMedia;

      return (
        <SocialMediaButton
          onClick={() => {
            const w = window.open(link, "_blank");
            w.focus();
          }}
          key={i}
        >
          <ButtonContent>
            <img
              alt="Social media icon."
              src={icon}
              srcSet={`${icon} 1x, ${icon2x} 2x, ${icon3x} 3x`}
            />
            {text}
          </ButtonContent>
        </SocialMediaButton>
      );
    })}
  </Wrapper>
);
