import React from "react";
import styled from "styled-components";

import facebookIcon from "./assets/icons/facebook_icon.png";
import facebookIcon2x from "./assets/icons/facebook_icon2x.png";
import facebookIcon3x from "./assets/icons/facebook_icon3x.png";
import instagramIcon from "./assets/icons/instagram_icon.png";
import instagramIcon2x from "./assets/icons/instagram_icon2x.png";
import instagramIcon3x from "./assets/icons/instagram_icon3x.png";

import { Button, Form, Header, Input, Quote, SocialMedia } from "./components";

import sizes from "./constants/sizes";

const StyledInput = styled(Input)`
  margin: 0 0 31px;
  width: 100%;

  @media (min-width: ${sizes.ss}) {
    margin: 0 13px 0 0;
  }
`;

const socialMedias = [
  {
    icon: instagramIcon,
    icon2x: instagramIcon2x,
    icon3x: instagramIcon3x,
    link: "https://www.instagram.com",
    text: "@Petite_Heimat"
  },
  {
    icon: facebookIcon,
    icon2x: facebookIcon2x,
    icon3x: facebookIcon3x,
    link: "https://www.facebook.com",
    text: "@PetiteHeimat"
  }
];

const TextContent = styled.p`
  font-size: 26px;
  letter-spacing: 0.52px;
  line-height: 31px;
  margin: 12% 0 3%;
  width: 65%;

  @media (max-width: ${sizes.m}) {
    font-size: 17px;
    letter-spacing: 0.34px;
    line-height: 20px;
    margin: 60px 0 0;
    width: 100%;
  }
`;

const MainContent = styled.section`
  padding: 9% 0 0 8%;

  @media (max-width: ${sizes.m}) {
    padding: 40px 28px 0;
  }
`;

export default () => (
  <>
    <Header title="Petite Heimat" />

    <MainContent>
      <Quote cite="- Alexandra Ludewig, Home Meets Heimat">
        Explorations of Heimat aid the socio-historical investigation of any
        society, as repositories of memory and history, escape and confrontation
        inscribed in Heimat can be read as signifiers of continuity and
        disruption, reorientation and return, and as such, ever-changing notions
        of Heimat mirror values and social change.
      </Quote>

      <TextContent>
        We’re out exploring and curating objects that have soul so you can
        create spaces that speak to your soul.
        <br />
        <br />
        Sign up to hear from us as soon as we launch!
      </TextContent>

      <Form>
        <StyledInput name="email" placeholder="Email" type="email" />
        <Button>Submit</Button>
      </Form>
    </MainContent>

    <SocialMedia socialMedias={socialMedias} />
  </>
);
