import React from "react";
import styled from "styled-components";

import sizes from "../../constants/sizes";
import firebase from "../../utils/firebase";

const Wrapper = styled.form`
  display: flex;
  margin-left: 5%;
  min-width: ${sizes.s};
  width: 35%;

  @media (max-width: ${sizes.m}) {
    margin-top: 4%;
  }

  @media (max-width: ${sizes.s}) {
    align-items: center;
    flex-direction: column;
    margin: 30px 0 0;
    min-width: 100%;
  }
`;

const dbRef = firebase.database().ref();
const emailsRef = dbRef.child("email");

const handleSubmit = e => {
  e.preventDefault();
  const email = e.target.email.value;

  emailsRef.once("value", snapshot => {
    const dbEmails = snapshot.val() && Object.values(snapshot.val());

    if (dbEmails && dbEmails.filter(item => item.email === email).length > 0) {
      alert("This email has been submited before!");
    } else {
      emailsRef.push().set({ email: email });
      alert("Thank you!"); // Should change that.
    }
  });
};

export default ({ children }) => (
  <Wrapper onSubmit={handleSubmit}>{children}</Wrapper>
);
